import React from "react";
import './Intro.css';
import Github from '../../img/github.png';
import Linkedin from '../../img/linkedin.png';
import instagram from '../../img/instagram.png';
import Vector1 from '../../img/Vector1.png';
import Vector2 from '../../img/Vector2.png';
import boy from '../../img/boy.png';
import thumbup from '../../img/thumbup.png';
import crown from '../../img/crown.png';
import glassesimoji from '../../img/glassesimoji.png';
import FloatingDiv from "../FloatingDiv/Floatingdiv";
import{motion} from 'framer-motion';


const Intro = () => {


const transition = {duration : 2, type:'spring'}

    return (
        <div className="Intro">
            <div className="i-left">
                <div className="i-name">
                    <span>Hy! I Am</span>
                    <span>Arpit Singh</span>
                    <span>
                        Frontend Developer with high level of Experience in web designing and development, producing the Quality work
                    </span>
                </div>
                <button className="button i-button">Hire me
                </button>
                <div className="i-icons">
                    <img src={Github} alt="" />
                    <img src={Linkedin} alt="" />
                    <img src={instagram} alt="" />
                </div>
            </div>
            <div className="i-right">
                <img src={Vector1} alt="" />
                <img src={Vector2} alt="" />
                <img src={boy} alt="" />
                <motion.img 
                initial={{left:'-36%'}}
                whileInView={{left:'-24%'}}
                transition={transition}
                 src={glassesimoji} alt="" />

                <motion.div
                initial={{top:'-4%', left:'74%'}}
                whileInView={{left:'68%'}}
                transition={transition}
                style={{ top: ' -4%', left: '68%' }}
                className='floating-div'
                >
                    <FloatingDiv image={crown} text1='Web' text2='Developer' />
                </motion.div>
                <motion.div 
                initial={{top:'9rem', left:'18rem'}}
                whileInView={{left:'0rem'}}
                transition={transition}
                style={{ top: '18.7rem', left: '0rem' }}
                 className='floating-motion.div'
                >
                    <FloatingDiv image={thumbup} text1='Best Design' text2='award' />
                </motion.div>
                {/* Blur effect home page */}
                <div className="blur1" style={{ background: "rgb(238 210 255)" }}>
                    <div className="blur2 blur1"></div>
                </div>
            </div>
        </div>
    )
}
export default Intro;