import React from "react";
import './TestMonial.css';
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from 'swiper';
import 'swiper/css/pagination';
import 'swiper/css';
import profile1 from "../../img/profile1.jpg";
import profile2 from "../../img/profile2.jpg";
import profile3 from "../../img/profile3.jpg";
import profile4 from "../../img/profile4.jpg";

const TestMonial = () => {

    const clients = [
        {
            img: profile1,
            review: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure tenetur sapiente quas perspiciatis distinctio veniam autem voluptatem ab, ."
        },
        {
            img: profile2,
            review: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure tenetur sapiente quas perspiciatis distinctio veniam autem voluptatem ab, ."
        },
        {
            img: profile3,
            review: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure tenetur sapiente quas perspiciatis distinctio veniam autem voluptatem ab, ."
        },
        {
            img: profile4,
            review: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Iure tenetur sapiente quas perspiciatis distinctio veniam autem voluptatem ab, ."
        },
    ]

    return (
        <div className="t-wrapper" id="TestMonial">
            <div className="t-heading">
                <span>Clients always get</span>
                <span> Exceptional Work</span>
                <span> form me...</span>
                <div className="blur1 t-blur1" style={{ background: 'var(--purple)' }}></div>
                <div className="blur1 t-blur2" style={{ background: 'skyblue' }}></div>

            </div>
            {/* slider */}
            <Swiper
                modules={[Pagination]}
                slidesPerView={1}
                pagination={{ clickable: true }}
            >
                {clients.map((client, index) => {
                    return <SwiperSlide key={index}>
                    <div className="testmonial">
                        <img src={client.img} alt="" />
                        <span>{client.review}</span>
                    </div>
                    </SwiperSlide>
                })}
            </Swiper>
        </div>
    )
}
export default TestMonial 