import React from "react";
import './navbar.css'
import { Link, } from 'react-scroll';


const Navbar = () => {
    return (
        <div className="n-wrapper">
            <div className="n-left">
                <div className="n-name">Aript-Sir</div>
                <span>Toggle</span>
            </div>
            <div className="n-right">
                <div className="n-list">
                    <ul>
                        <Link spy={true} to='Navbar' smooth={true} activeClass='activeClass'><li className="hov">Home</li></Link>
                        <Link spy={true} to='Services' smooth={true} activeClass='activeClass'><li className="hov">Services</li></Link>
                        <Link spy={true} to='Experience' smooth={true} activeClass='activeClass'><li className="hov">Experience</li></Link>
                        <Link spy={true} to='Portfolio' smooth={true} activeClass='activeClass'><li className="hov">PortFolio</li></Link>
                        <Link spy={true} to='TestMonial' smooth={true} activeClass='activeClass'><li className="hov">TesMonial</li></Link>
                    </ul>
                </div>
                <Link spy={true} to='Contact' smooth={true} activeClass='activeClass'><button className='button n-button'>
                    Contact
                </button></Link>

               
            </div>

        </div>
    )
}
export default Navbar;