import React from "react";
import './Service.css';
import heartemoji from '../../img/heartemoji.png';
import glasses from '../../img/glasses.png';
import humble from '../../img/humble.png';
import Card from "../Card/Card";
import resume from './resume.pdf'; 
import { motion } from "framer-motion";
const Service = () => {

const transition = {duration : 1, type:'spring'}

    return (
        <div className="services" id="Services">
            <div className="awesome">
                <span> My Awesome </span>
                <span> Services </span>
                <span>Lorem,  dolor   consectetur adipisicing  Reiciendis <br />quod rem laudantium quibusdam do ea </span>
                <a href={resume} download>      <button className="button s-button">Download CV</button> </a>
                <div className="blur1 s-blur1" style={{ background: "#abf1ff94" }}></div>
            </div>
            <div className="cards">
                <motion.div
                initial={{ left:'25%'}}
                whileInView={{left:'25rem'}}
                transition={transition}
                 style={{ left: '23rem' }}>
                    <Card
                        emoji={heartemoji}
                        heading={'Design'}
                        deatil={"Figma, Sketch, Photoshop,adobe, Adobe xd"}
                    />
                </motion.div>
                <div style={{ top: '12rem', left: '5rem' }}>
                    <Card
                        emoji={glasses}
                        heading={'Developer '}
                        deatil={"Html,Javascript, react Mongo ,node.js express.js"}
                    />
                </div>
                <motion.div 
                initial={{ left:'22%'}}
                whileInView={{left:'22rem'}}
                transition={transition}
                style={{ top: '19rem', left: '21rem' }}>
                    <Card
                        emoji={humble}
                        heading={'UI/UX'}
                        deatil={"LORE AFAF  JIAFJ IOA FHIO IOAFH IOAFH  AF"}
                    />
                </motion.div>

            </div>
        </div>
    )
}
export default Service;